<template>
  <div>
    <LoadingSpinner v-if="loading" />

    <div v-if="errorMessage">{{ errorMessage }}</div>
    <div class="FetchJobsWidth">
      <div class="search-container">
        <input
          class="search-input"
          v-model="searchTerm"
          placeholder="Search" />
        <button
          @click="searchAndFilterJobs"
          class="filter-jobs-btn">
          Search
        </button>
      </div>
      <button
        @click="filterJobs"
        class="filter-jobs-btn">
        Filter Working Links
      </button>

      <div id="card_container">
        <div
          class="card"
          v-for="item in jobs"
          :key="item.id">
          <div class="card-img">
            <button class="label">NEW</button>
          </div>

          <div class="card-desc">
            <div class="card-margin">
              <div class="card-job-main-info">
                <div class="font-size-company-name white-space">
                  {{
                    item.company.length >= 55
                      ? item.company.substring(0, 55) + "..."
                      : item.company
                  }}
                </div>

                <div class="font-size-job-description white-space">
                  {{
                    item.name.length >= 55
                      ? item.name.substring(0, 55) + "..."
                      : item.name
                  }}
                </div>

                <div class="font-size-job-id white-space">
                  Job ID :- {{ item._id }}
                </div>
              </div>

              <div class="job-details">
                <div class="job-card-detail white-space">
                  Location : {{ item.actualCity }},
                  {{
                    item.address.length >= 55
                      ? item.address.substring(0, 55) + "..."
                      : item.address
                  }}
                </div>

                <div class="job-card-detail white-space">
                  Skills : {{ item.technologies.slice(0, 3) }}
                </div>

                <div class="job-card-detail white-space">
                  Salary : {{ item.annualSalaryFrom }} -
                  {{ item.annualSalaryTo }}
                </div>

                <div class="Center">
                  <div class="job-tags">
                    <div class="tag white-space">{{ item.companyType }}</div>
                    <div class="tag white-space">{{ item.expLevel }}</div>
                    <div class="tag white-space">{{ item.jobType }}</div>
                  </div>
                </div>
              </div>
              <v-divider></v-divider>
              <div class="links FlexSpaceBetween">
                <div class="Center">
                  <a
                    class="btn-apply"
                    :href="`${item.redirectJobUrl}`"
                    target="_blank"
                    >Apply</a
                  >
                </div>

                <div class="Center">
                  <a
                    class="btn-website"
                    :href="`https://www.${item.companyWebsiteLink}`"
                    target="_blank"
                    >Website</a
                  >
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  // import axios from "axios";
  import LoadingSpinner from "../components/LoadingSpinner.vue";
  import data from "./data.json";

  export default {
    data() {
      return {
        jobs: [],
        loading: true,
        errorMessage: null,
        searchTerm: "",
      };
    },
    mounted() {
      this.getJobs();
    },
    methods: {
      searchAndFilterJobs() {
        if (!this.searchTerm) {
          this.jobs = data;
        } else {
          this.jobs = data.filter((job) =>
            job.name.toLowerCase().includes(this.searchTerm.toLowerCase())
          );
        }
      },
      filterJobs() {
        this.jobs = this.jobs.filter(
          (item) => item.redirectJobUrl && item.companyWebsiteLink
        );
      },
      getJobs() {
        try {
          this.loading = true;
          this.jobs = data;
          this.loading = false;
        } catch (err) {
          console.log(err);
          this.loading = false;
          this.errorMessage = err;
        }
      },
    },
    components: {
      LoadingSpinner,
    },
  };
</script>
