<template>
  <v-container class="MarginBottom-120">
    <LoadingSpinner v-if="loading" />
    <div class="Banner">
      <v-banner
        outlined
        rounded
        shaped
        sticky
        tile>
        <v-icon
          slot="icon"
          color="warning"
          size="36">
          mdi-alert
        </v-icon>
        <div class="red--text">
          This project's scope is too broad. I'm working on a role-based job
          edit and delete option, which I'll include in the next update. Thank
          You.
        </div>

        <template v-slot:actions="{ dismiss }">
          <v-btn
            text
            color="deep-purple accent-4"
            @click="dismiss">
            Dismiss
          </v-btn>
        </template>
      </v-banner>
    </div>
    <v-row>
      <v-col
        sm="10"
        class="pa-4 mx-auto">
        <template>
          <v-card
            class="mx-auto"
            max-width="800"
            outlined>
            <v-list-item three-line>
              <v-list-item-content>
                <div class="text-h4 purpleTheme">
                  {{ post.companyNameTitle }}
                </div>
                <v-list-item-title class="text-h5">
                  {{ post.jobProfile }}
                </v-list-item-title>
                <v-list-item-subtitle>{{ post.location }}</v-list-item-subtitle>
              </v-list-item-content>
              <v-list-item-avatar
                tile
                size="130"
                color="white"
                ><v-img :src="`/${post.image}`"> </v-img
              ></v-list-item-avatar>
            </v-list-item>
            <v-card-actions>
              <v-list-item-subtitle class="ml-2 grey--text">
                Skills : {{ post.skills }}</v-list-item-subtitle
              >
            </v-card-actions>
            <v-card-actions>
              <v-list-item-subtitle class="ml-2 grey--text">
                Salary : {{ post.salary }}</v-list-item-subtitle
              >
            </v-card-actions>
            <v-card-actions>
              <v-list-item-subtitle class="ml-2 grey--text">
                Email : {{ post.CompanyEmail }}
              </v-list-item-subtitle>
            </v-card-actions>
            <v-card-actions>
              <v-row class="d-flex justify-space-between mt-4 mb-2">
                <v-btn
                  text
                  color="deep-purple accent-4"
                  :href="`${post.CompanyLink}`"
                  target="_blank">
                  Apply
                </v-btn>
                <v-btn
                  color="success"
                  text
                  :to="{ name: 'edit-post', params: { id: post._id } }"
                  >Edit</v-btn
                >
                <v-btn
                  color="red"
                  text
                  @click="removePost(post._id)"
                  >Delete</v-btn
                >
                <div>
                  <v-btn
                    color="orange"
                    text
                    @click="show = !show">
                    Job Description
                  </v-btn>
                  <v-btn
                    icon
                    @click="show = !show">
                    <v-icon>{{
                      show ? "mdi-chevron-up" : "mdi-chevron-down"
                    }}</v-icon>
                  </v-btn>
                </div>
              </v-row>
            </v-card-actions>
            <v-expand-transition>
              <div v-show="show">
                <v-divider></v-divider>
                <v-card-text>
                  {{ post.jobDescription }}
                </v-card-text>
              </div>
            </v-expand-transition>
          </v-card>
        </template>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  import API from "../api";
  import LoadingSpinner from "../components/LoadingSpinner.vue";
  export default {
    data() {
      return {
        post: {},
        show: false,
        loading: false,
      };
    },
    components: {
      LoadingSpinner,
    },
    async created() {
      this.loading = true;
      const response = await API.getPostByID(this.$route.params.id);
      this.post = response;
      this.loading = false;
    },
    methods: {
      async removePost(id) {
        this.loading = true;
        const response = await API.deletePost(id);
        this.loading = false;
        this.$router.push({
          name: "JobsView",
          params: { message: response.message },
        });
      },
    },
  };
</script>
<style scoped>
  .Banner {
    background-color: white;
    margin-top: -30px;
    margin-bottom: 100px;
    border-radius: 8px;
  }
</style>
